.nav{grid-area:nav;background-color:#1b1b1b;display:grid;grid-template:10vh 1fr / 1fr}.nav .nav_logo,.nav .nav_logo:hover,.nav .nav_logo::after,.nav .nav_logo:hover .nav_logo::after{-webkit-transition:color 0.33s 0s ease-out,width 0.33s 0s ease-in-out,text-shadow 0.33s 0s ease-in-out;-o-transition:color 0.33s 0s ease-out,width 0.33s 0s ease-in-out,text-shadow 0.33s 0s ease-in-out;transition:color 0.33s 0s ease-out,width 0.33s 0s ease-in-out,text-shadow 0.33s 0s ease-in-out}.nav .nav_logo{color:#fff;line-height:var(--nav-logo-height);text-align:center;position:relative}.nav .nav_logo::after{content:"";position:absolute;left:50%;bottom:0;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%);width:0px;height:10px;background-color:var(--nav-colors-first);will-change:width}.nav .nav_logo:hover{color:var(--nav-colors-first);text-shadow:0px 0px 2px var(--nav-colors-first)}.nav .nav_logo:hover::after{width:100%}.nav .nav_links{display:grid;grid-template:repeat(calc(var(--nav-links-amount) + 1), var(--nav-link-size))/1fr}.nav .nav_links .nav_path__link,.nav .nav_links .nav_path__link:hover,.nav .nav_links .nav_path__link::after,.nav .nav_links .nav_path__link:hover::after{-webkit-transition:color .33s 0s ease-out;-o-transition:color .33s 0s ease-out;transition:color .33s 0s ease-out}.nav .nav_links .nav_path{grid-area:auto;line-height:var(--nav-link-size);text-align:center}.nav .nav_links .nav_path__link{height:var(--nav-link-font-size);color:#fff;text-decoration:none;font-size:var(--nav-link-font-size);position:relative}.nav .nav_links .nav_path__link::after{content:"";content:var(--nav-link-after-content);color:#fff;text-align:center;font-size:calc(var(--nav-link-after-height) / 1.2);line-height:var(--nav-link-after-height);position:absolute;left:50%;bottom:calc(var(--nav-link-after-height) * -1);-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%);width:100%;height:var(--nav-link-after-height)}.nav .nav_links .nav_path__link:hover,.nav .nav_links .nav_path__link:focus{outline:none;color:var(--nav-colors-sec)}.nav .nav_links .nav_path__link:hover::before,.nav .nav_links .nav_path__link:focus::before{text-shadow:0px 0px 4.5px var(--nav-colors-sec)}.nav .nav_links .nav_path__link:hover::after,.nav .nav_links .nav_path__link:focus::after{color:var(--nav-colors-sec)}



